@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro:400,700");
body{
  color:#FFF;
  font-size: 14px;
  font-family: "Source Code Pro", monospace;
  background-color:#111;
  width:600px;
  margin:0 auto;
  margin-top:1%;
}
li,ul{
  list-style:none;
  display:inline;
  padding:0px;
  margin:0px;
}
.nav{
  padding-top:2px;
  padding-bottom:2px;
}
h2,h4{
  text-decoration: none;
  margin:0px;
  padding:0px;
}
a:visited,a{
  padding-left:1px;
  padding-right:1px;
  color:#FFF;
  text-decoration: none;
  margin-right:10px;
}
a:hover{
  color:#333;
  background-color:#FFF;
}
.blogpost{

  font-size:12px;
  text-decoration:none;
  color:#FFF;

}
